import {Device, CortexReg, MachineState, machineStateToString} from 'dapjs';
import HID from 'webhid';
import {K64F} from './targets'

function logMachineState(lbl: string) {
    return (s: MachineState) => {
        console.log(machineStateToString(s).replace(/^/gm, lbl + ": "))
        return s
    }
}

$(() => {
    let hid: HID;
    let dev: Device;
    let cm: K64F;

    $('#click').click(() => {
        navigator.usb.requestDevice({ filters: [{vendorId: 0x0d28}]})
            .then(dev => {
                hid = new HID(dev);
                return hid.open();
            })
            .then(() => {
                $('#connect').prop('disabled', false);
            });
    });

    $('#connect').click(() => {
        dev = new Device(hid);
        cm = new K64F(dev);

        let st: MachineState;

        cm.init()
            .then(() => cm.halt())
            // .then(() => {
            //     return cm.writeCoreRegister(CortexReg.SP, 0x1FFF0000);
            // })
            .then(() => cm.snapshotMachineState())
            .then(logMachineState("init"))
            .then(s => {
                console.log('Resuming core.')
                cm.resume();
            })
            .catch((e) => {
                console.error(e);
                return dev.close();
            });
    });

    $('#flash').click(() => {
        // flash the microcontroller we have connected.
        console.log('Flashing mcu ;)');

        cm.flashInit()
            .then((r0) => {
                return cm.eraseChip();
            })
            .then(() => {
                dev.close();
            });
    });
});



